.lr-page input[type="submit"] {
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    padding: 12px 20px;
    background: var(--color-primary);
    border-radius: 8px;
    letter-spacing: 0.5px;
    line-height: 1;
    text-transform: uppercase;
    color: var(--white);
    text-decoration: none;
    cursor: pointer;
    transition: all linear .3s;

    &:hover {
        background: var(--color-primary-dark);
        color: var(--white);
    }

    @media (max-width: 767px) {
        // padding: 8px 20px;
    }
}

.input-with-icon {
    position: relative;
    margin-top: 4px;

    & input {

    }

    & svg {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 10px;
        right: 8px;
        visibility: hidden;
        opacity: 0;
        cursor: pointer;
        transition: 0.3s all;

        & path {
            fill: var(--color-svg-input);
        }

        &.active {
            visibility: visible;
            opacity: 1;
        }
    }
}
